.background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: -1;
}

.container {
    padding: 20px;
}

.folder,
.item {
	display: flex;
	align-items: center;
    gap: 10px;
    margin-bottom: 10px;
    font-size: 24px;
}

.folder {
	cursor: pointer;
}

.hide {
	display: none;
}

.show {
	display: flex;
}

.items {
	padding-left: 10px;
}

.loader-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    font-size: 24px;
}

.loader {
    width:  80px;
    height: 80px;
}